<template>
    <form action="">
        <div class="modal-card" style="width: 400px">
            <header class="modal-card-head">
                <p class="modal-card-title">Nueva Promotora</p>
            </header>
            <section class="modal-card-body">
                <b-field label="Descripcion:">
                    <b-input type="textarea" :value="descripcion" placeholder="Breve descripción de la promotora"
                        required>
                    </b-input>
                </b-field>

                <b-field label="Tipo">
                    <b-select placeholder="Ej.: A">
                        <option v-for="tipo in tipos" :value="tipo.id" :key="tipo.id">
                            {{ tipo.descripcion }}
                        </option>
                    </b-select>
                </b-field>

                <b-field class="file">
                    <b-upload v-model="files">
                        <a class="button is-primary">
                            <b-icon icon="upload"></b-icon>
                            <span>Subir Foto</span>
                        </a>
                    </b-upload>
                    <span class="file-name" v-if="files && files.length">
                        {{ files[0].name }}
                    </span>
                </b-field>
            </section>
            <footer class="modal-card-foot">
                <button class="button is-danger" type="button" @click="$parent.close()">Cancelar</button>
                <button class="button is-primary"><b-icon icon="floppy"></b-icon><span>Guardar</span>   </button>
            </footer>
        </div>
    </form>
</template>

<script>
    export default {
        name: "PromoModal",
        props: [],
        data() {
            return {
                tipos: [
                    { id: 1, descripcion: "A" },
                    { id: 2, descripcion: "B" },
                    { id: 3, descripcion: "C" }
                ]
            };
        }
    }
</script>


<style scoped>

</style>


