<template>
  <div>
    <b-modal :active.sync="isComponentModalActive" has-modal-card>
      <promoModal v-bind="formProps" canCancel></promoModal>
    </b-modal>
    <nav class="navbar is-primary">
      <div class="navbar-brand">
        <div class="navbar-item">
          <p class="is-uppercase is-size-4 has-text-weight-semibold">Promo Catalog</p>
        </div>
      </div>
      <div class="navbar-menu">
        <div class="navbar-start">

        </div>
        <div class="navbar-end">
          <div class="navbar-item has-dropdown is-hoverable">
            <a href="" class="navbar-link">
              <span class="icon is-large">
                <i class="fa fa-user-circle fa-2x" aria-hidden="true"></i>
              </span>
              <span class="has-text-weight-semibold">Ady Taveras | Admin</span>
            </a>

            <div class="navbar-dropdown">
              <a class="navbar-item" href="#">
                <span class="icon is-medium">
                  <i class="fa fa-id-card fa-lg"></i>
                </span>
                <span class="is-size-5">
                  Editar Perfil
                </span>
              </a>

              <a class="navbar-item" href="#">
                <span class="icon is-medium">
                  <i class="fa fa-sign-out-alt fa-lg"></i>
                </span>
                <span class="is-size-5">
                  Cerrar Sesión
                </span>
              </a>
            </div>
          </div>
        </div>
      </div>
    </nav>

    <section class="section" id="options">
      <div class="box">
        <b-button type="is-success" @click="isComponentModalActive = true">Crear Promotora</b-button>
      </div>
    </section>

    <section id="page" class="section">

      <div class="columns">

        <div class="column">
          <div class="box">
            <b-table :data="data" ref="table" paginated per-page="5" :opened-detailed="defaultOpenedDetails" detailed
              detail-key="id" @details-open="(row, index) => $toast.open(`Viendo: ${row.promotora.codigo}`)"
              :show-detail-icon="showDetailIcon" aria-next-label="Next page" aria-previous-label="Previous page"
              aria-page-label="Page" :has-mobile-cards="true" aria-current-label="Current page">

              <template slot-scope="props">
                <b-table-column field="promotora.codigo" label="Código" width="60" sortable>
                  <template v-if="showDetailIcon">
                    {{ props.row.promotora.codigo }}
                  </template>
                  <template v-else>
                    <a @click="toggle(props.row)">
                      {{ props.row.promotora.codigo }}
                    </a>
                  </template>
                </b-table-column>
                <b-table-column field="promotora.edad" label="Edad" sortable>
                  {{ props.row.promotora.edad }}
                </b-table-column>
                <b-table-column field="promotora.nacionalidad" label="Nacionalidad" sortable>
                  {{ props.row.promotora.nacionalidad }}
                </b-table-column>
                <b-table-column field="promotora.tipo" label="Tipo" sortable>
                  {{ props.row.promotora.tipo }}
                </b-table-column>
              </template>

              <template slot="detail" slot-scope="props">
                <div class="columns">
                  <div class="column is-3">
                    <figure>
                      <p class="image">
                        <img :src="props.row.promotora.photo">
                      </p>
                    </figure>
                  </div>
                  <div class="column">
                    <article class="media">
                      <div class="media-content">
                        <div class="content">
                          <p>
                            <strong>{{ props.row.promotora.codigo }}</strong>
                            <br>
                            {{ props.row.promotora.descripcion }}
                          </p>
                        </div>
                      </div>
                    </article>
                  </div>
                </div>
              </template>
            </b-table>
          </div>
        </div>
      </div>
    </section>
  </div>

</template>

<script>
  import promoModal from './Promo-Modal.vue';
  
  const data = [
    {
      id: 1,
      promotora:
      {
        codigo: "CP000001",
        edad: 27,
        nacionalidad: "Dominicana",
        tipo: "A",
        descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit.Proin ornare magna eros, eu pellentesque tortor vestibulum ut.Maecenas non massa sem. Etiam finibus odio quis feugiat facilisis.",
        photo: "/static/img/promo1.jpg"
      }
    },
    {
      id: 2,
      promotora:
      {
        codigo: "CP000002",
        edad: 23,
        nacionalidad: "Venezolana",
        tipo: "C",
        descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit.Proin ornare magna eros, eu pellentesque tortor vestibulum ut.Maecenas non massa sem. Etiam finibus odio quis feugiat facilisis.",
        photo: "/static/img/promo2.jpg"
      }
    },
    {
      id: 3,
      promotora:
      {
        codigo: "CP000032",
        edad: 19,
        nacionalidad: "Dominicana",
        tipo: "A",
        descripcion: "Lorem ipsum dolor sit amet, consectetur adipiscing elit.Proin ornare magna eros, eu pellentesque tortor vestibulum ut.Maecenas non massa sem. Etiam finibus odio quis feugiat facilisis.",
        photo: "/static/img/promo3.jpg"
      }
    }

  ]

  export default {
    name: 'Home',
    components: {
      promoModal
    },
    data() {
      return {
        data,
        defaultOpenedDetails: [1],
        showDetailIcon: true,
        isComponentModalActive: false,
        descripcion: null,
        photopath: ""
      }
    },
    methods: {
      toggle(row) {
        this.$refs.table.toggleDetails(row)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #options {
    padding-bottom: 0;
  }
</style>


